.App {
    font-family: sans-serif;
    text-align: center;
  }
  
  .item {
    max-width: 80%;
    margin: 10px auto;
    padding: 20px;
    border-radius: 10px;
    font-weight: bold;
    background-color: black;
    color: white;
  }
  button{
    margin-left: 10px ;
  }
  .item:hover {
    background-color: darkslategrey;
  }
  
  .item:active {
    background-color: lightslategray;
  }
  